@use 'base/variables' as v;
@use 'base/mixins' as m;


.testimoniales{
    @include m.tablet{
        @include m.grid(5, 4rem);
        align-items: center;
    }

    &__imagen{
        grid-column: 1/3;
    }
    &__contenido{
        grid-column: 3/6;
    }
    &__heading{
        @include m.headingBorder(v.$secundario, v.$primario);
    }
    &__paragraph{
        background-color: v.$primario;
        border: .5rem solid v.$secundario;
        margin-bottom: 4rem;
        padding: 2rem;
        border-radius: 1rem;
        position: relative;

        @include m.tablet{
            max-width: 70%;

            &:nth-child(2){
                margin-left: 10%;
            }
            &:nth-child(3){
                margin-left: 20%;

            }
            &:nth-child(4){
                margin-left:30% ;
            }

        }

        &::before{
            content: '';
            display: block;
            width: 5rem;
            height: 5rem;
            background-image: url(../img/comilla.svg);
            background-repeat: no-repeat;
            background-position: center center;
            position: absolute;


        }
        &:last-of-type{
            margin-bottom: 0;
        }

    }
    &__descripcion{
        margin: 0;
        padding-left: 5rem;

    }
}